<template>
  <div style="height: 100vh; width: 100%; display: flex; justify-content: center; align-items: center; background-color: #b2b2e3">
    <div style="display: flex; background-color: white;width: 65%; border-radius: 5px; overflow: hidden;">
      <div style="flex: 1">
        <img src="../assets/register.jpg" alt="" style="width: 100%;height: 100%">
      </div>
      <div style="flex: 1; display: flex; align-items: center; justify-content: center;">
        <el-form :model="form" style="width: 80%" :rules="rules" ref="forgotRef">
          <div style="font-size: 20px; font-weight: bold; text-align: center; margin-bottom: 20px">找回密码</div>
          
          <el-form-item prop="username">
            <el-input prefix-icon="el-icon-user" size="medium" placeholder="请输入账号" v-model="form.username"></el-input>
          </el-form-item>

          <el-form-item prop="code" style="margin-bottom: 10px">
            <div style="display: flex">
              <el-input prefix-icon="el-icon-message" placeholder="请输入验证码" v-model="form.code" style="flex: 2"></el-input>
              <el-button type="primary" style="flex: 1; margin-left: 10px" @click="sendCode" :disabled="codeDisabled">
                {{ codeBtnText }}
              </el-button>
            </div>
          </el-form-item>

          <el-form-item prop="password">
            <el-input prefix-icon="el-icon-lock" size="medium" show-password placeholder="请输入新密码" v-model="form.password"></el-input>
          </el-form-item>

          <el-form-item prop="confirmPass">
            <el-input prefix-icon="el-icon-lock" size="medium" show-password placeholder="请确认新密码" v-model="form.confirmPass"></el-input>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" style="width: 100%" @click="submit">提交</el-button>
          </el-form-item>

          <div style="text-align: center; margin-top: 20px">
            <el-button style="color: #409EFF; cursor: pointer" @click="$router.push('/login')">返回登录</el-button>
          </div>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ForgotPassword",
  data() {
    const validatePassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请确认密码'));
      } else if (value !== this.form.password) {
        callback(new Error('两次输入的密码不一致'))
      } else {
        callback()
      }
    }
    
    return {
      form: {
        username: '',
        code: '',
        password: '',
        confirmPass: ''
      },
      codeDisabled: false,
      codeBtnText: '获取验证码',
      countdown: 60,
      rules: {
        username: [
          { required: true, message: '请输入账号', trigger: 'blur' }
        ],
        code: [
          { required: true, message: '请输入验证码', trigger: 'blur' },
          { min: 6, max: 6, message: '验证码为6位', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入新密码', trigger: 'blur' },
          { min: 6, message: '密码长度不能小于6位', trigger: 'blur' }
        ],
        confirmPass: [
          { validator: validatePassword, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    sendCode() {
      if (!this.form.username) {
        this.$message.warning('请输入账号')
        return
      }
      
      // 模拟发送验证码请求
      this.$request.post('/sendCode', { username: this.form.username })
        .then(res => {
          if (res.code === '200') {
            this.startCountdown()
            this.$message.success('验证码已发送')
          } else {
            this.$message.error(res.msg)
          }
        })
    },

    startCountdown() {
      this.codeDisabled = true
      const timer = setInterval(() => {
        this.countdown--
        this.codeBtnText = `${this.countdown}秒后重发`
        if (this.countdown <= 0) {
          clearInterval(timer)
          this.codeBtnText = '获取验证码'
          this.codeDisabled = false
          this.countdown = 60
        }
      }, 1000)
    },

    submit() {
      this.$refs['forgotRef'].validate(valid => {
        if (valid) {
          this.$request.post('/forget', this.form).then(res => {
            if (res.code === '200') {
              this.$message.success('密码重置成功')
              this.$router.push('/login')
            } else {
              this.$message.error(res.msg)
            }
          })
        }
      })
    }
  }
}
</script>

